import { Component } from '@angular/core';
import { WindowRef } from './../window.ref';
import { Csv } from './../csv.ref';

@Component({
  selector: 'app-users-list',
  template: `
    <div class="main-block text-center">
      <button routerLink="/" class="btn btn-primary">Go To Landing</button>
      <div class="mt-2 text-left">
        <button class="btn btn-danger" [disabled]="checkedItems.length == 0" (click)="this.removeChecked()">Delete</button>
        <button class="btn btn-success ml-1" [disabled]="checkedItems.length == 0" (click)="this.getCsv()">Download</button>
      </div>
      <div class="mt-1 text-left" *ngIf="this.checkedItems.length > 0">
        <span>Selected {{ this.checkedItems.length }}</span>
      </div>  
      <table *ngIf="this.getUsers()" class="mt-4 text-left mb-4 users-table" style="width: 100%">
        <tr>
          <th class="py-2 text-primary"><strong>Check</strong></th>
          <th class="py-2 text-primary"><strong>Email</strong></th>
          <th class="py-2 text-primary"><strong>First Name</strong></th>
          <th class="py-2 text-primary"><strong>Last Name</strong></th>
          <th class="py-2 text-primary"><strong>Country</strong></th>
          <th class="py-2 text-primary"><strong>Date Of Birth</strong></th>
          <th class="py-2 text-primary"><strong>Details</strong></th>
          <th class="py-2 text-primary"><strong>Delete</strong></th>
          <th class="py-2 text-primary"><strong>Modal</strong></th>
        </tr>
        <tr app-user-item *ngFor="let user of this.getUsers() | slice :(page - 1) * pageSize :pageSize * page" 
          [user]="user"
          (click)="this.toggleCheck(user.id);this.updateCheckedItems()"></tr>
      </table>
      <ngb-pagination *ngIf="this.getUsers()" [collectionSize]="this.getUsers().length" [(page)]="page" [boundaryLinks]="true"></ngb-pagination>
    </div>
  `,
  styles: [
    `
      .users-table tr:nth-of-type(even) {
        background-color: #07152f;
      }
      .users-table tr:not(:first-of-type):hover {
        background-color: #5077ca;
        cursor: pointer;
      }
    `
  ]
})

export class UsersListComponent {
  length: number;
  checkedItems = [];

  page = 1;
  pageSize = 10;

  constructor(
    private winRef: WindowRef, 
    private Csv: Csv
  ) {}
  
  public getUsers() {
    return this.winRef.getUsers();
  }

  public getLenght() {
    return this.winRef.getUsers().length;
  }

  /**
   * Toggles the param isChecked on the user item
   * @param id 
   */
  toggleCheck(id) {
    let user = this.winRef.getUsers().filter(user => {
      return user.id === id;
    })[0];

    user.isChecked = !(user.isChecked !== 'undefined' && user.isChecked == true);
  }

  /**
   * Updates the number of the checked items
   */
  updateCheckedItems() {
    let checkedUsers = this.winRef.getUsers().filter(user => {
      return typeof user.isChecked !== 'undefined' && user.isChecked == true;
    });

    this.checkedItems = checkedUsers;
  }
  
  /**
   * Removes the selected items from the list
   * 
   * @TODO The implementation doesn't smell good, better find a way to improve it
   */
  removeChecked() {
    let userIds = this.checkedItems.map(user => {
      return user.id;
    });

    let keys = [];
    this.winRef.getUsers().forEach((user, key) => {
      if (userIds.includes(user.id)) {
        keys.push(key);
      }
    })

    let deleteOffset = 0;
    keys.forEach(key => {
      this.winRef.getUsers().splice(key - deleteOffset, 1);
      deleteOffset++;
    })
    
    this.checkedItems = [];
  }

  /**
   * Downloads the selected options as csv
   */
  getCsv() {
    let headers = {
      id: 'id',
      firstName: "firstName",
      lastName: "lastName",
      email: "email",
      country: "country",
      dateOfBirth: "dateOfBirth"
    };

    this.checkedItems.forEach(user => {
      this.toggleCheck(user.id);
    })

    this.Csv.exportCSVFile(headers, this.checkedItems, 'Users');
    this.checkedItems = [];
  }
}
