import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';

import { WindowRef } from '../window.ref';

@Component({
  selector: 'app-user-details',
  template: `
    <div class="main-block text-center">
      <button routerLink="/users" (click)="$event.stopPropagation()" class="btn btn-primary mb-2">Go To List</button>
      <div *ngIf="this.getUsers()">
        <h1 class="mb-2">{{this.getUser().firstName + ' ' + this.getUser().lastName}}</h1>
        <table style="width: 100%">
          <tr>
            <th class="text-primary"><strong>ID</strong></th>
            <th class="text-primary"><strong>Email</strong></th>
            <th class="text-primary"><strong>Country</strong></th>
            <th class="text-primary"><strong>Date Of Birth</strong></th>
          </tr>
          <tr>
            <th>{{this.getUser().id}}</th>
            <th>{{this.getUser().email}}</th>
            <th>{{this.getUser().country}}</th>
            <th>{{this.getUser().dateOfBirth | date: longFormat}}</th>
          </tr>
        </table>
      </div>
    </div>
  `,
})

export class UserDetailsComponent {
  user: object;
  userForm = new FormGroup({
    userName: new FormControl(''),
    userLastName: new FormControl(''),
    userEmail: new FormControl(''),
    userCountry: new FormControl(''),
    userDateOfBirth: new FormControl('')
  });

  constructor(private route: ActivatedRoute, private winRef:WindowRef) {}

  getUsers() {
    return this.winRef.getUsers();
  }

  getUser() {
    if (typeof this.user === 'undefined') {
      this.route.params.subscribe(params => {
        this.user = this.winRef.getUsers().find(user => {
          return +user.id === +params['id']
        });
      });
    }

    return this.user;
  }
}
