export class UserItemComponent {
  @Input() user: object;

  constructor(private winRef: WindowRef) {}

  removeUser() {
    this.winRef.getUsers().forEach((item, key) => {
      if (this.user['id'] == item.id) {
        this.winRef.getUsers().splice(key, 1);
      }
    });
  }
}
